<script>
import { saveAs } from "file-saver";

export default {
  data() {
    return {
      // 定时器
      loop: 0,
      clickShow: false,
      longClickShow: false,
      //存储动力学参数
      tableDate: [],
      tableDate1:[],
      pre_tableDate: [],
      joint: [],
      //实现进度条
      num: 0,
      mutex1: 1,
      mutex2: 0,
      my_url: 0,
      Width: {
        width: "0px"
      },
      isShow: false
    };
  },

  methods: {
    async init() {
      this.my_url =
        "http://" + window.location.hostname + ":" + "10005/robot/v1.0/";
    },

    start_processlider() {
      console.log(this.mutex1);
      console.log(this.mutex2);
      var id = setInterval(() => {
        this.isShow = true;
        this.num++;
        this.Width = {
          width: this.num + "px"
        };
        if (this.num >= 100) {
          this.num = 100;
          clearInterval(id);
          // this.isShow = false;
          this.mutex2 = 0;
        }
      }, 800);
    },
    stop_processlider() {
      this.mutex1 = 1;
      this.mutex2 = 0;
    },

    async get_dynamics_identify_result() {
      this.init();
      console.log("-----------get dynamics result---------------");
      this.$axios
        .post(this.my_url + "get_dynamics_identify_result")
        .then(response => {
          let Base64 = require("js-base64").Base64;

          this.pre_tableDate = response.data.data.fileData.data;
          this.tableDate1 = Base64.decode(this.pre_tableDate);
          
          this.tableDate =this.tableDate1.split("\t");
          console.log(this.tableDate);
        })
        .catch(response => {
          console.log(response);
        });
    },

    downloadTxt() {
      this.get_dynamics_identify_result();
      let str = this.tableDate;
      let strData = new Blob([str], { type: "text/plain;charset=utf-8" });
      saveAs(strData, "动力学参数.txt");
    },

    async get_dynamics_identify_start_pos() {
      this.init();
      console.log("-----------get dynamics start pos---------------");
      this.$axios
        .post(this.my_url + "get_dynamics_identify_start_pos")
        .then(response => {
          this.joint[1] = response.data.data.jointpos.joint1;
          this.joint[2] = response.data.data.jointpos.joint2;
          this.joint[3] = response.data.data.jointpos.joint3;
          this.joint[4] = response.data.data.jointpos.joint4;
          this.joint[5] = response.data.data.jointpos.joint5;
          this.joint[6] = response.data.data.jointpos.joint6;
        })
        .catch(response => {
          console.log(response);
        });
    },

    start() {
      this.init();
      this.stop_processlider();
      this.get_dynamics_identify_start_pos();
      console.log("-----------start moving---------------");
      this.$axios
        .post(this.my_url + "movj", {
          movjreq: {
            jointpos: {
              joint1: this.joint[1],
              joint2: this.joint[2],
              joint3: this.joint[3],
              joint4: this.joint[4],
              joint5: this.joint[5],
              joint6: this.joint[6]
            },
            velocity: 50
          }
        })
        .then(response => {
          this.newsList = response.data.data;
        })
        .catch(response => {
          console.log(response);
        });
    },

    stop() {
      this.stop_processlider();
      this.init();
      console.log("-----------stop moving---------------");
      this.$axios
        .post(this.my_url + "jog_stop", {
          intid: 0
        })
        .then(response => {
          this.newsList = response.data.data;
        })
        .catch(response => {
          console.log(response);
        });
    },

    get_dynamics_data() {
      this.init();
      console.log("-----------get data over---------------");
      this.$axios
        .post(this.my_url + "heart_beat")
        .then(response => {
          this.newsList = response.data.data;
        })
        .catch(response => {
          console.log(response);
        });
    },

    start_identify_dynamics() {
      this.init();
      this.mutex2 = 1;
      console.log(this.mutex1);
      console.log(this.mutex2);
      if (this.mutex1 == 1 && this.mutex2 == 1) {
        this.start_processlider();
      }
      this.mutex1 = 0;
      this.mutex2 = 0;
      console.log("-----------start identify dynamics---------------");
      this.$axios
        .post(this.my_url + "start_identify_dynamics")
        .then(response => {
          this.newsList = response.data.data;
        })
        .catch(response => {
          console.log(response);
        });
    },

    stop_identify_dynamics() {
      this.init();
      console.log("-----------stop identify dynamics---------------");
      this.$axios
        .post(this.my_url + "stop_identify_dynamics")
        .then(response => {
          this.newsList = response.data.data;
        })
        .catch(response => {
          console.log(response);
        });
    },

    get_dynamics_identify_status() {
      this.init();
      console.log("-----------get dynamics identify status---------------");
      this.$axios
        .post(this.my_url + "get_dynamics_identify_status")
        .then(response => {
          this.newsList = response.data.data;
        })
        .catch(response => {
          console.log(response);
        });
    },

    handlerTouchstart() {
      this.start();
      this.get_dynamics_identify_start_pos();
      this.loop = setTimeout(() => {
        this.get_dynamics_identify_start_pos();
        this.loop = 1; // 执行长按要执行的内容
        this.start();
        this.stop_processlider();

        this.num = 0;
        this.clickShow = false;
        this.longClickShow = true;
      }, 10000000); // 定时的时间
      return false;
    },

    handlerTouchmove() {
      // 清除定时器
      clearTimeout(this.loop);
      this.loop = 0;
    },

    handlerTouchend() {
      // 清除定时器
      clearTimeout(this.loop);
      if (this.loop !== 0) {
        // 单击操作
        this.clickShow = true;
        this.longClickShow = false;
      }
      this.stop();
    }
  }
};
</script>


